.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
}

.estilosTitulos {
  text-align: center;
  margin-top: 20px;
}

.tituloTienda {
  background-color: violet;
}

.subtituloPrincipal {
  text-decoration: underline wavy violet;
}

.cartWidget {
  color: white;
  font-size: 40px;
  margin-left: 20px;
  margin-right: 20px;
}

.contenedorBoton {
  margin-top: 5%;
  border-color: black;
  padding: 3%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.botonCount {
  border-radius: 10%;
  border-color: violet;
  background-color: black;
  color: violet;
  padding: 10px;
  font-size: 15px;
}

.contenedorItemCount p {
  font-size: 20px;
}

.itemContainer {
  width: 50%;
  margin: 0 auto;
  /*background-color: rgb(243, 198, 243);*/
  margin-top: 10%;
}

.itemContainer:hover{
  background-color: rgb(243, 198, 243);
}

.itemImg {
  width: 50%;
  margin: 0 auto;
}

.itemImg img {
  width: 90%;
  margin: 0 auto;
  border-radius: 10%;
  margin-top: 3%;
}

.itemInfo {
  text-align: center;
}

.containerItemDetail {
  width: 90%;
  margin: 0 auto;
  margin-top: 10%;
  background-color: rgb(230, 210, 184);
}

.imgItemDetail {
  display: flex;
  width: 90%;
  margin: 0 auto;
  margin-top: 5%;
}

.imgItemDetail img {
  display: flex;
  width: 50%;
  height: 70%;
  margin-top: 5%;
}

.itemDetail {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-content: space-around;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}

.cardsItems {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-end;
  justify-content: space-around;
  align-items: center;
}

.spinner {
  text-align: center;
  color: #282c34;
  font-size: 24px;
}

.botonInactivo {
  border-radius: 10%;
  border-color: grey;
  background-color: rgb(0, 0, 0, 0.5);
  color: rgb(0, 0, 0);
  padding: 10px;
  font-size: 15px;
}

.botonCheckout {
  border-radius: 10%;
  border-color: black;
  background-color:violet;
  color: black;
  font-weight: bold;
  padding: 10px;
  font-size: 15px; 
}

.cartContainer {
  display: grid;
  grid-template-columns: 3fr 50% 2fr;
}

.imgCart img {
  width: 60%;
  margin: 0 auto;
  margin-top: 10%;
  margin-left: 20%;
}

.cartDetail {
  text-align: center;
  align-self: center;
}

.precioCart {
  text-align: center;
  align-self: center;
}

.cartText {
  text-align: center;
  margin-top: 2%;
}

.cartSubText {
  text-align: center;
  font-weight: bold;
  font-style: italic;
  font-size: 1.2rem;
  margin-top: 2%;
}

.contenedorBotonCart {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: space-around;
  justify-content: space-evenly;
  margin-top: 5%;
  margin-bottom: 5%;
}

.containerCompra {
  border: solid purple;
  padding: 2%;
  width: 70%;
  margin: 0 auto;
  margin-top: 10%;
  margin-bottom: 10%;
}

.compraText {
  display: flex;
  justify-content: space-between;
  margin-top: 5%;
  margin-bottom: 5%;
}